@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.spinner-svg {
  animation: spin 0.8s infinite forwards;
  width: 10rem;
  height: 10rem;
}

